html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  color: var(--title-text-color);
}
a {
  text-decoration: none;
}
ul {
  display: flex;
  list-style: none;
  margin-left: 0;
  padding-left: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin-top: 0;
  margin-bottom: 0;
}
section {
  padding-top: 60px;
  padding-bottom: 60px;
  @media screen and (min-width: 1200px) {
    padding-top: 94px;
    padding-bottom: 94px;
  }
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #e7ecf2;
}
