.profile {
  width: 320px;
  margin-top: 40px;
  margin-bottom: 40px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 5px 5px 8px 1px rgba(138, 130, 130, 0.75);
  -webkit-box-shadow: 5px 5px 8px 1px rgba(138, 130, 130, 0.75);
  -moz-box-shadow: 5px 5px 8px 1px rgba(138, 130, 130, 0.75);
}
.description {
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #fff;
}
.avatar {
  display: flex;
  width: 120px;
  border-radius: 50%;
  background-color: #e7ecf2;
  margin: 20px auto;
}
.name {
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 16px;
  color: #202e40;
}
.tag {
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  color: #c6ccd2;
  margin-bottom: 16px;
}
.location {
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  color: #c6ccd2;
  margin-bottom: 20px;
}
.stats {
  display: flex;
  justify-content: center;
  padding: 0;
  background-color: #f3f6f9;
}
.stat {
  display: flex;
  width: calc(320px / 3);
  padding: 12px 0px;
  flex-direction: column;
  align-items: center;
}
.stat:not(:last-child) {
  border-right: 1px solid;
  border-color: #e4e9f0;
}
.stat:hover {
  background-color: #7a848f;
}
.label {
  font-size: 14px;
  font-weight: 500;
  color: #b4bdc6;
  margin-bottom: 12px;
}
.quantity {
  font-size: 16px;
  font-weight: 700;
  color: #202e40;
}
